* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
html {
    scroll-behavior: smooth;
}
body {
    background-color: #0e0e0f;
    color: #dfdfdf;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans",
        "Helvetica Neue", sans-serif;
    margin: 0;
    /* width is weird?? there is horz scroll bars */
    font-size: 18px;
}
a,
.fa-inverse,
.link-circle {
    text-decoration: none;
    color: #a0a0a0;
    transition-duration: 0.2s;
}
.fa-inverse {
    color: #ffffff;
}
a:hover,
.fa-inverse:hover {
    color: #e94560;
}
.bar {
    z-index: 1;
    display: flex;
    width: 100%;
    position: fixed;
    padding: 1rem;
    background-color: #1b191a;
}
.bar-btn {
    margin: 1rem;
}
h2 {
    color: #ffffff;
}
.wrapper {
    display: grid;
}
.bigsection {
    padding: 5rem;
    padding-bottom: 0rem;

    width: 100%;
}
#ba {
    background-image: url("./images/night.jpg");
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: white;
    padding: auto 5rem;
    height: 100vh;
}
.section {
    padding: 2rem;
    padding-right: 2.5rem;
    border: 4px solid;
}
#sa {
    width: 500px;
    max-width: 90%;
    backdrop-filter: blur(2px);
    animation: fadein 2s;
}
@keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
#name {
    font-size: 3rem;
    width: 100%;
    color: #ffffff;
}

.blurb {
    margin-top: 0.5rem;
    width: 100%;
}
.experiences {
    position: relative;
    margin: 2rem 0.5rem 0rem;
    padding: 1.2rem;
    background-color: #232325;
    border-radius: 10px;
    box-shadow: 0.5rem 0.5rem #1a191b;
    width: 100%;
    font-size: 1.2rem;
}
.exp-title {
    display: block;
    font-size: 2rem;
    max-width: 85%;
    word-break: keep-all;
}
.exp-years {
    display: block;
    position: absolute;
    top: 1.2rem;
    right: 1.2rem;
    color: #a5a5a5;
    font-size: 1.5rem;
    font-style: italic;
}
.after-float::after {
    clear: both;
    height: 0;
    width: 100%;
    content: "";
    display: block;
}
.exp-description {
    margin-top: 2rem;
    white-space: pre-line;
}

.proj-desc-container {
    display: flex;
    width: 100%;
    align-items: flex-start;
    margin-top: 1rem;
    column-gap: 1rem;
}
.proj-img {
    object-fit: cover;
    border-radius: 10px;
    border: 4px solid;
    max-width: 40%;
}
.proj-description {
    white-space: pre-line;
}
.proj-icons {
    position: absolute;
    right: 1.2rem;
    bottom: 1.2rem;
    display: block;
}
.proj-icon {
    font-size: 3rem;
    margin: 1rem;
}
#contact {
    display: inline-block;
    margin-left: 50%;
    transform: translateX(-50%);
}
#sd {
    border: none;
}
#bd {
    padding-top: 0;
}
.contact-links-container {
    display: flex;
    margin-top: 3rem;
    align-items: center;
    justify-content: center;
}
.contact-link {
    margin-left: 2rem;
    margin-right: 2rem;
}
.particles {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
}
