@media only screen and (max-width: 900px) {
    .bigsection {
        padding: 1rem;
    }
    #ba {
        background-attachment: scroll;
    }
    #sa {
        margin: auto;
        margin-top: 5rem;
        width: 500px;
        max-width: 90%;
        position: relative;
    }
    .exp-title {
        font-size: 1.5rem;
        width: 70%;
    }
    .proj-desc-container {
        display: block;
        width: 100%;
        margin-top: 1rem;
        column-gap: 1rem;
    }
    .proj-description {
        margin-top: 1rem;
    }
    .proj-img {
        object-fit: cover;
        border-radius: 10px;
        border: 4px solid;
        width: 100%;
        max-width: 100%;
    }
    .proj-icons {
        position: relative;
        display: inline-block;
        height: 100%;
        right: auto;
        bottom: auto;
        margin-left: 50%;
        transform: translateX(-50%);
        margin-top: 2rem;
    }
    /* Particle dimensions are incorrect on mobile for some reason. For now, just don't display them on mobile. */
    .particles {
        display: none;
    }
}
